export default function( $ ) {
	if ( $( '.cubes' ).length > 0 ) {
		const cubes = $( '.cubes' );
		const pageHeight = cubes.height();
		let top = 1100;
		let leftA = 1;
		let leftB = 2;
		let leftC = 1;

		function cubeA() {
			let classA = 'cube-left';
			if ( leftA % 2 === 0 ) {
				classA = 'cube-right';
			}
			const cubeHtml = '<div class="cube cube-a ' + classA + '" style="top: ' + top + 'px;"><div class="cube__inner" data-stellar-ratio="2" data-stellar-vertical-offset="10"></div></div>';
			cubes.append( cubeHtml );
			leftA++;
		}

		function cubeB() {
			let classB = 'cube-left';
			if ( leftB % 2 === 0 ) {
				classB = 'cube-right';
			}
			const cubeHtml = '<div class="cube cube-b ' + classB + '" style="top: ' + top + 'px;"><div class="cube__inner" data-stellar-ratio="0.5" data-stellar-vertical-offset="10"></div></div>';
			cubes.append( cubeHtml );
			leftB++;
		}

		function cubeC() {
			let classC = 'cube-left';
			if ( leftC % 2 === 0 ) {
				classC = 'cube-right';
			}
			const cubeHtml = '<div class="cube cube-c ' + classC + '" style="top: ' + top + 'px;"><div class="cube__inner" data-stellar-ratio="1.5" data-stellar-vertical-offset="10"></div></div>';
			cubes.append( cubeHtml );
			leftC++;
		}

		do {
			cubeA();
			top += 1200;
			cubeB();
			top += 800;
			cubeC();
			top += 1000;
		} while ( top < pageHeight );
	}
}
