export default function( $ ) {
	if ( $( '.about__programs' ).length > 0 ) {
		$( '.about__program' ).on( 'click', function() {
			$( '.about__program' ).removeClass( 'active' );
			$( this ).addClass( 'active' );
			const $text = $( this ).html();
			const $details = $( this ).closest( '.about__programs' ).find( '.about__programs__details' );
			$details.html( $text );
		} );

		$( '.about__program.active' ).trigger( 'click' );

		function aboutAdjustCss() {
			if ( $( window ).width() >= 992 ) {
				const bgLeft = $( '.about__bg' ).offset().left;
				const infoLeft = $( '.about__info' ).offset().left;
				const infoDelta = infoLeft - bgLeft;
				let paddingLeft = 0;
				if ( infoDelta < 40 ) {
					if ( infoDelta < 0 ) {
						paddingLeft = -infoDelta + 40;
					} else {
						paddingLeft = 40;
					}
					$( '.about__info' ).css( 'padding-left', paddingLeft + 'px' );
				} else {
					$( '.about__info' ).css( 'padding-left', '' );
				}

				const programsLeft = $( '.about__programs' ).offset().left;
				const programsRight = programsLeft + $( '.about__programs__list' ).outerWidth();
				const detailsLeft = $( '.about__programs__details' ).offset().left;
				const detailsDelta = detailsLeft - programsRight;
				let marginRight = 0;
				if ( detailsDelta < 50 ) {
					if ( detailsDelta < 0 ) {
						marginRight = -detailsDelta + 40;
					} else {
						marginRight = 40;
					}
					$( '.about__programs' ).css( 'margin-right', -marginRight + 'px' );
				} else {
					$( '.about__programs' ).css( 'margin-right', '' );
				}
			} else {
				$( '.about__programs' ).css( 'margin-right', '' );
				$( '.about__info' ).css( 'padding-left', '' );
			}
		}
		aboutAdjustCss();
		$( window ).resize( function() {
			aboutAdjustCss();
		} );
	}
}
