export default function( $ ) {
	function resizeImgBlock() {
		if ( $( '.about-statistics__images' ).length > 0 ) {
			if ( $( window ).width() >= 992 ) {
				const textHeight = $( '.about-statistics__inner' ).outerHeight();
				const imgHeight = textHeight + 182 + 66; // top & bottom margins
				const imgWidth = imgHeight * 0.697;
				const w = ( imgWidth / $( '.about-statistics' ).outerWidth() ) * 100; // image block width in %
				const left = 50 - w;

				if ( left < 0 ) {
					$( '.about-statistics__images' ).css( 'width', w + '%' );
					$( '.about-statistics__images' ).css( 'left', left + '%' );
					$( '.about-statistics__images' ).css( 'padding-top', w + '%' );
				} else {
					$( '.about-statistics__images' ).css( 'width', '' );
					$( '.about-statistics__images' ).css( 'left', '' );
					$( '.about-statistics__images' ).css( 'padding-top', '' );
				}
			} else {
				$( '.about-statistics__images' ).css( 'width', '' );
				$( '.about-statistics__images' ).css( 'left', '' );
				$( '.about-statistics__images' ).css( 'padding-top', '' );
			}
		}
	}

	if ( $( '.about-statistics__slide' ).length > 1 ) { // slick will be activated
		setTimeout( function(){
			resizeImgBlock();
		}, 500 ); // slick slider needs time to initialize
		$( window ).resize( function() {
			setTimeout( function(){
				resizeImgBlock();
			}, 500 );
		} );
	} else {
		resizeImgBlock();

		$( window ).resize( function() {
			resizeImgBlock();
		} );
	}

	if ( $( '.about-statistics__slider' ).length > 0 ) {
		const statisticsSlider = $( '.about-statistics__slider' );
		const sliderControls = statisticsSlider.closest( '.about-statistics' ).find( '.slick-controls' );
		const prev = sliderControls.find( '.slick-prev ');
		const next = sliderControls.find( '.slick-next ');
		statisticsSlider.on( 'init', function() {
			sliderControls.removeClass( 'd-none' );
		} );
		statisticsSlider.slick( {
			slidesToShow: 1,
			slidesToScroll: 1,
			infinite: false,
			dots: false,
			arrows: true,
			autoplay: false,
			prevArrow: prev,
			nextArrow: next,
			adaptiveHeight: false,
			responsive: [
				{
					breakpoint: 991,
					settings: {
						adaptiveHeight: true,
					},
				},
			],
			init: resizeImgBlock(),
		} );
	}
}
