/**
 * Our Main entry point for Theme's frontend JS code.
 */

import jQuery from 'jquery';
import objectFit from './utils/object-fit'; // importing default exported element - free name specified here.
import { bodyJsClass } from './utils/js'; // imported named export.

// set JS classes on body.
bodyJsClass();

// call object fit images with dependency injected jQuery;
objectFit( jQuery );

// Slick slider
import 'slick-carousel';

// Header menu
import headerMenu from './components/header-menu';
headerMenu( jQuery );

// Announcement Bar
import announcement from './components/announcement-bar';
announcement( jQuery );

// Header bg when the page is scrolled
import headerScroll from './components/header-scroll';
headerScroll( jQuery );

// Video block
import playVideo from './components/video';
playVideo( jQuery );

// Audio Button
import audioBtn from './components/audio-btn';
audioBtn( jQuery );

// Accordion block
import accordion from './components/accordion';
accordion( jQuery );

// Gallery block - connecting Fancybox
import galleryFancybox from './components/gallery-fancybox';
galleryFancybox( jQuery );

// Gallery block - connecting PF script
import { initGalleryBlocks } from './components/gallery-block';
initGalleryBlocks();

// Tabs block
import tabs from './components/tabs';
tabs( jQuery );

// Slider block
import slider from './components/slider';
slider( jQuery );

// Reviews block
import reviews from './components/reviews';
reviews( jQuery );

// Blog Filter
import blogFilter from './components/category-filter';
blogFilter( jQuery );

// Scroll to Element
import { initScrollToElement, scrollToElement } from './components/scroll-to-element';
initScrollToElement(); // initialize a.scroll-to hash links if necessary.

// about block
import aboutUs from './components/about';
aboutUs( jQuery );

// about-statistics block
import aboutStatistics from './components/about-statistics';
aboutStatistics( jQuery );

// about-statistics block
import aboutCamp from './components/about-camp';
aboutCamp( jQuery );

// table-stack
import tableStack from './components/table-stack';
tableStack( jQuery );

// page-nav
import pageNav from './components/page-nav';
pageNav( jQuery );

// stellar.js
import stellarJS from './components/jquery.stellar';
stellarJS( jQuery, window, document, undefined );

// stellar.js settings
import stellarSettings from './components/stellar-settings';
stellarSettings( jQuery );

// cubes on bg
import cubes from './components/cubes';
cubes( jQuery );
