export default function( $ ) {
	if ( $( '.about-camp__slider' ).length > 0 ) {
		$( '.about-camp__slider' ).each( function() {
			const prev = $( this ).siblings( '.slick-controls' ).find( '.slick-prev ');
			const next = $( this ).siblings( '.slick-controls' ).find( '.slick-next ');
			$( this ).on( 'init', function( event, slick ){
				$( this ).closest( '.about-camp__slider__wrapper' ).find( '.slick-controls' ).removeClass( 'd-none' );
			} );
			$( '.about-camp__slider' ).slick( {
				slidesToShow: 1,
				slidesToScroll: 1,
				infinite: false,
				dots: false,
				arrows: true,
				autoplay: false,
				prevArrow: prev,
				nextArrow: next,
			} );
		} );

		function aboutCampAdjustCss() {
			if ( $( window ).width() >= 992 ) {
				const activityRight = $( '.about-camp__activity' ).offset().left + $( '.about-camp__activity' ).outerWidth();;
				const activityDelta = $( '.about-camp__bg' ).outerWidth() - activityRight;
				let right = 0;
				if ( activityDelta < 80 ) {
					right = $( '.about-camp' ).outerWidth() - activityRight - 80;
					if ( right < 0 ) {
						right = 0;
					}
					$( '.about-camp__bg' ).css( 'right', right + 'px' );
				} else {
					$( '.about-camp__bg' ).css( 'right', '' );
				}
			} else {
				$( '.about-camp__bg' ).css( 'right', '' );
			}
		}
		aboutCampAdjustCss();
		$( window ).resize( function() {
			aboutCampAdjustCss();
		} );
	}
}
